.MainContainer{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0);
    border-bottom: 0.5px solid rgba(175, 175, 175, 0);
    z-index: 5;
    backdrop-filter: blur(0px);
    transition: ease-in-out 0.2s;
}
.MainContainer.NotTop{
    background-color: rgba(0, 0, 0, 0.685);
    border-bottom: 0.5px solid rgba(175, 175, 175, 0.425);
    backdrop-filter: blur(5px);
}
.NavBarContainer{
    
    width: 95%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.left{
    width: calc(30%);
    height: 100%;

    display: flex;
    align-items: center;
}
.Logo{
    height: 60%;
}
.logoText{

    color: #fff;

}
.logoText h2{
    transform: scaleY(0.7);
}
.logoText h4{
    font-weight: 100;
    font-size: 15px;
    transform: scaleY(0.7);
}
.right{
    width: calc(70%);
    height: 100%;
    display: flex;
    justify-content: flex-end;
    
    display: flex;
    align-items: center;
    gap: 10px;
}
.NavLink{
    color: #fff;
    text-decoration: none;
    text-shadow: 1px 1px 2px rgb(0, 0, 0);
    font-size: 20px;
    font-weight: 400;
    transform: scaleY(1.3);
    padding: 5px 10px;
    border-bottom: 0.5px solid rgb(177, 34, 34, 0);
    transition: ease-in-out 0.2s;
    white-space: nowrap;
}
.NavLink:hover{
    color: rgb(233, 20, 20);
    /* border-bottom: 0.5px solid rgb(177, 34, 34); */
}