.image{
    position: relative;
    z-index: 1;
    border-top: 3px solid rgb(90, 8, 8);
    padding-top: 5px;
    overflow: hidden;
}
.CardDetail{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer{
    position: absolute;
    bottom: 0;
    left: 0;
    margin-top: 50px;
    width: 100%;
    background-color: rgb(99, 7, 7);
    border-bottom: 1px solid rgba(90, 8, 8, 0.63);
    font-size: 15px;
    font-weight: 100;
    padding: 10px 0px 10px 5px;
    
}