.NewsMainContainer{
    color: #fff;
    width: 100%;
   
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.803);
    border-top: rgba(255, 255, 255, 0.336) solid 1px;
}
.newsContainer{
    padding: 100px 0px;
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.newsHeader{
    font-size: 50px;
    font-weight: 300;
    text-align: left;
}
.newsList{
    display: flex;
    flex-direction: row;
    width: 100%;
    gap: 20px;
}
.cardItem{
    width: calc(100%/4);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 5px;
    position: relative;

    padding-bottom: 25px;
}
.thumbnailContainer{
     width: 100%;
     max-height: 100%;
     border: 0.5px solid rgb(94, 94, 94);
    overflow: hidden;
}

.thumbnail{
   width: 100%;
   
    transition: 0.3s ease-in-out;
}
.thumbnail:hover{
    transform: scale(1.3);
}
.newsDetail{
    font-weight: 100;
    padding-top: 0px;
    padding-bottom: 20px;
    color: #fff;
    text-decoration: none;
    text-align: left;
}
.newsDetail:hover{
    text-decoration: underline;
}
.newsFooter{
    display: flex;
    gap: 5px;
    position: absolute;
    bottom: 0;
}
.newsTime{
    font-weight: 100;
    font-size: 15px;
    color: rgb(255, 24, 24);
}
.newsCategory{
    font-weight: 100;
    font-size: 15px;
    color: rgb(255, 255, 255);
    text-decoration: none;
}
.newsCategory:hover{
    text-decoration: underline;
}

@media (max-width: 500px){
    .newsList{
        flex-direction: column;

    }
    .cardItem{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 5px;
        position: relative;

        padding-bottom: 25px;
    }
}
    