@mixin flexCenter() {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin flexStart() {
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

@mixin flexEnd() {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

@mixin Button {
    width: 100%;
    background-color: rgb(45, 42, 253);
    color: #fff;
    font-size: 17px;
    font-weight: bold;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 8px 10px;
    text-decoration: none;
    cursor: pointer;
    &:hover {
        background-color: rgb(103, 101, 243);
        color: #fff;
    }
    &.Red {
        background-color: rgb(253, 42, 42);
        color: #fff;
        &:hover {
            background-color: rgb(243, 101, 101);
            color: #fff;
        }
    }
    &.Yellow {
        background-color: rgb(194, 123, 31);
        color: #fff;
        &:hover {
            background-color: rgb(218, 187, 88);
            color: #fff;
        }
    }
}

@mixin TextBox {
    padding: 5px;
    font-size: 15px;
    background-color: rgb(23, 23, 46);
    outline: none;
    border: 1px solid rgb(42, 74, 116);
    border-radius: 5px;
    height: 40px;
    color: rgb(70, 159, 211);
    &:focus {
        border: 1px solid rgb(51, 106, 226);
    }
}

@mixin Scroll {
    &::-webkit-scrollbar {
        width: 5px;
    }
    &::-webkit-scrollbar-track {
        background: rgb(23, 23, 46);
    }
    &::-webkit-scrollbar-thumb {
        background: rgb(16, 52, 168);
    }

}

@mixin Panel {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background-color: rgba(11, 27, 46, 0.781);
    backdrop-filter: blur(3px);


}
@mixin Panel_Sub {
    position: absolute;
    background-color: rgb(31, 31, 99);
    box-shadow: 0 0 10px rgba(24, 70, 107, 0.568);
    border: 1px solid rgb(52, 111, 189);
    padding: 20px;

}
@mixin Panel_Header {
    padding: 5px 0;
    @include flexCenter;
    color: rgb(70, 159, 211);
    font-weight: 100;
    font-size: 20px;

}

@mixin ClosePanel {

    gap: 5px;
    position: absolute;
    width: max-content;
    top: 20px;
    right: 20px;
    background-color: transparent;

}
