@import "../../../Styles/style";


.MainSnipCode {

    height: 100vh;
    width: 100%;
    @include flexCenter;
    align-items: flex-start;
    background-color: rgb(39, 17, 75);
    .Menu {

        max-width: $width_menu;
        height: 100%;

        border-right: 1px solid rgb(39, 35, 97);
        display: flex;
        flex-direction: column;
        .Header {
            height: 70px;
            font-size: 25px;
            @include flexCenter;
            color: #fff;
            font-weight: 100;
            gap: 5px;
            .Icon {
                background-color: #fff;
                color: rgb(39, 17, 75);
                padding: 5px;
                font-size: 40px;
                border-radius: 10px;
            }
        }
        .List {
            height: inherit;
            width: 100%;
            color: #fff;
            text-align: left;

            display: flex;
            flex-direction: column;

            .Section {
                height: 100%;
                display: flex;
                flex-direction: column;
                .Header {
                    width: 100%;
                    height: 30px;
                    font-size: 15px;
                    @include flexStart;
                    padding: 5px;
                    font-weight: bold;
                    background-color: rgb(35, 47, 99);
                }
                .DetailList {
                    height: 100%;
                    overflow: hidden;
                    overflow-y: auto;
                    background-color: rgba(0, 0, 0, 0.459);
                    .List {
                        display: flex;
                        flex-direction: column;
                        gap: 2px;
                        .MenuLink {
                            cursor: pointer;
                            font-weight: 100;
                            padding: 5px 10px;
                            transition: ease-in-out 0.2s;
                            @include flexStart;
                            align-items: flex-start;
                            gap: 5px;
                            border-left: 5px solid rgba(45, 64, 170, 0.801);
                            color: rgb(180, 225, 252);
                            .Icon {
                                font-size: 20px;
                            }
                            &:hover {
                                background-color: rgba(76, 75, 160, 0.548);
                                // padding-left: 20px;
                                gap: 20px;
                                padding-left: 20px;
                            }
                        }
                    }
                }

            }

        }
        .Utilities {
            max-height: 200px;
            @include flexCenter;
            flex-direction: column;
            align-items: flex-start;
            width: 100%;
            .Header {
                width: 100%;
                height: 30px;
                font-size: 15px;
                @include flexStart;
                padding: 5px;
                font-weight: bold;
                background-color: rgb(35, 47, 99);
            }
            .List {

                background-color: rgba(0, 0, 0, 0.459);
                @include flexCenter;
                @include Scroll;
                align-items: flex-start;
                width: 100%;
                height: 100%;

                overflow: hidden;
                overflow-y: auto;
                .UtilityDetail {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    gap: 2px;
                    .Button {
                        height: 30px;
                        font-size: 15px;
                        text-align: left;
                        font-weight: 100;
                        @include flexStart;
                        background-color: transparent;
                        border-left: 5px solid rgba(45, 64, 170, 0.801);
                        border-radius: 0;
                        transition: ease-in-out 0.2s;
                        color: rgb(180, 225, 252);
                        &.hide {
                            display: none;
                        }
                        &:hover {
                            background-color: rgba(69, 67, 190, 0.452);
                            padding-left: 20px;

                        }
                    }
                }

            }
        }
        .Controller {
            padding: 10px;
            @include flexCenter;
            flex-direction: column;
            gap: 5px;
            .Button {
                @include Button;
                &.hide {
                    display: none;
                }
            }
        }
    }
    .Content {
        position: relative;
        width: inherit;
        height: 100%;
        overflow: hidden;
        overflow-y: auto;
        overflow-x: auto;
        @include Scroll;
        background-color: rgb(23, 23, 46);
        padding-top: 60px;
        color: rgb(149, 195, 221);
        .Header {
            top: 0;
            left: $width_menu;
            right: 0;
            position: fixed;
            @include flexEnd;
            justify-content: space-between;
            color: rgb(70, 159, 211);
            padding: 10px;
            gap: 5px;
            border-bottom: 1px solid rgb(45, 45, 83);
            background-color: rgb(23, 23, 46);

            .TitleSnipCode {
                @include flexStart;
                font-weight: 500;
                gap: 5px;
                font-size: 20px;
            }
            .Right {
                @include flexEnd;
                gap: 5px;
                .Button {

                    height: 35px;
                    max-width: 35px;
                    @include flexCenter;
                    @include Button;
                    font-size: 30px !important;
                    &.hide {
                        display: none;
                    }

                }
            }

        }
        .Detail {
            text-align: left;
            padding: 0 10px;

        }
    }
}
.AddNoteMainContainer {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 20px;
    height: 100vh;
    background-color: rgba(21, 15, 39, 0.942);
    backdrop-filter: blur(5px);
    visibility: visible;
    opacity: 1;
    &.Show {
        visibility: visible;
        opacity: 1;
        transition: ease-in 0.1s;
    }
    .Controller {
        @include flexCenter;

        gap: 5px;
        position: absolute;
        width: max-content;
        right: 20px;
        background-color: transparent;

    }
    .Header {
        font-size: 20px;
        font-weight: 100;
        height: 50px;
        color: rgb(70, 159, 211);
    }
    .DetailContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        height: calc(100% - 50px);

        .Input {
            @include TextBox;
            // width: 100%;
            // padding: 5px 10px;
            // border: 1px solid rgb(70, 70, 70);
            // outline: none;
            // background-color: rgb(23, 23, 46);
            // color: rgb(70, 159, 211);
            // &:focus {
            //     border: 2px solid rgb(45, 42, 253);
            // }
        }
        .Content {
            @include TextBox;
            // border: 1px solid rgb(70, 70, 70);
            padding: 5px 10px;
            text-align: left;
            height: inherit;
            overflow: hidden;
            overflow-y: auto;

            // outline: none;
            // &:focus {
            //     border: 2px solid rgb(45, 42, 253);
            // }
        }
        .Controller {
            height: 50px;

        }
    }
}
.bdr {
    border: 1px solid rgb(248, 43, 43);
}
.Button {
    @include Button;
}

.AuthenticationMainContainer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background-color: rgba(11, 27, 46, 0.781);
    backdrop-filter: blur(3px);


    &.active {
        visibility: visible;
        opacity: 1;
    }
    .AuthenticationContainer {
        position: absolute;
        width: 300px;

        background-color: rgb(31, 31, 99);
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 0 10px rgba(24, 70, 107, 0.568);
        border: 1px solid rgb(52, 111, 189);
        padding: 20px;
        // visibility: hidden;
        // opacity: 0;
        // transition: ease-in-out 0.3s;

        &.active {
            top: 50%;
            visibility: visible;
            opacity: 1;
        }
        .Header {
            padding: 5px 0;
            @include flexCenter;
            color: rgb(70, 159, 211);
            font-weight: 100;
            font-size: 17px;
        }
        .AuthenticationForm {
            padding: 20px 0;

            @include flexCenter;
            flex-direction: column;
            gap: 10px;
            input {
                width: 100%;
                @include TextBox;
            }
        }
        .Controller {
            @include flexCenter;
            gap: 10px;
        }
    }
}
