@import "../../../../Styles/style";

.YarnMainContainer {
    @include Panel;
    @include flexCenter;
    .Controller {
        @include ClosePanel;

    }
    .MainContainer {
        @include Panel_Sub;
        width: 900px;
        height: max-content;
        display: flex;
        flex-direction: column;
        .Header {
            @include Panel_Header;
        }
        .AdminTool {
            @include flexCenter;
            gap: 5px;
            input {
                @include TextBox;
            }
            select {
                @include TextBox;
            }
            button {
                @include Button;
                width: max-content;
            }
            &.hide {
                display: none;
            }
        }
        .List {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            height: inherit;
            padding: 10px 0;
            gap: 10px;
            .Section {
                .SectionHeader {
                    background-color: rgb(32, 64, 133);
                    color: rgb(129, 202, 231);
                    height: 30px;
                    font-weight: 500;
                    cursor: pointer;
                    &:hover {
                        background-color: rgb(39, 81, 170);
                        color: rgb(129, 202, 231);
                    }
                }
                .SectionList {
                    @include TextBox;
                    @include Scroll;
                    height: 100%;
                    height: 500px;
                    overflow: hidden;
                    overflow-y: auto;
                    .YarnDetail {
                        text-align: left;
                        padding: 5px 0;
                        @include flexStart;
                        gap: 5px;
                        justify-content: space-between;
                        input {

                            width: 15px;
                            height: 15px;
                            background-color: transparent;
                        }
                        .Left {
                            @include flexStart;
                            gap: 5px;
                            cursor: pointer;
                            &:hover {
                                background-color: rgba(69, 67, 190, 0.452);
                            }
                        }
                        .Right {
                            .Button {
                                @include Button;
                                @include flexCenter;
                                width: 20px;
                                height: 20px;
                                font-size: 10px;
                                &.hide {
                                    display: none;
                                }
                            }
                        }
                    }


                }
            }
        }
        .YarnCode {
            @include TextBox;
            height: fit-content;
            cursor: pointer;
            &:hover {
                background-color: rgb(22, 68, 99);
                color: rgb(129, 202, 231);
            }
            &:active {
                background-color: rgb(33, 103, 150);
                color: rgb(129, 202, 231);
            }
        }

    }
}
