.TitleLineupMainContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.871);
    border-top: rgba(255, 255, 255, 0.336) solid 1px;
}
.TitleLineupContainer {
    width: 80%;
    display: flex;
    flex-direction: column;
    
    padding: 100px 0;
    gap: 40px;
}
.HeaderMain{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.TitleLineupDetail {
    display: flex;
    border: 1px solid rgba(255, 11, 11, 0.295);
    padding: 20px;

}
.TitleLineupDetail .left {

    width: calc(40%);

    display: flex;
    justify-content: center;
    align-items: flex-start;
    ;
}
.TitleLineupDetail .center{
    width: calc(40%);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
}
.TitleLineupDetail .center .StoryHeader{
    
    width: 90%;
    
    font-size: 30px;
}
.TitleLineupDetail .center .StoryContent{
    transform: translateX(-30px);
    opacity: 0;
    width: 90%;
    display: flex;
    font-size: 20px;
    font-weight: 100;
    text-align: justify;
    
}
.TitleLineupDetail .center .StoryContent.active{
    transform: translateX(0px);
    opacity: 1;
    transition: ease-in 0.5s;
    
}
.TitleLineupDetail .right {

    width: calc(20%);

    display: flex;
    justify-content: center;
    align-items: flex-start;
    overflow: hidden;


}
.TitleLineupThumbContainer{

    width: 100%;


    display: flex;
    align-items: center;
}
.TitleLineupThumb {
    width: 100%;
    filter: brightness(0);   
    
}
.TitleLineupThumb.active{
    filter: brightness(1);
    transition: ease-in 0.3s;
}
.TitleContainer {

    width: 100%;


    display: flex;
    gap: 1px;
    flex-direction: column;

    height: 100%;

}

.TitleLink {
    font-size: 20px;
    font-weight: 100;
    text-shadow: 0px 0px 3px rgb(0, 0, 0);
    background-image: linear-gradient(90deg, rgb(94, 14, 14), rgba(255, 0, 0, 0));

    color: rgb(255, 208, 208);
    text-align: left;
    width: calc(100% );

    cursor: pointer;
    white-space: nowrap;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    border-left: 5px solid rgba(63, 7, 7, 0.911);
    
    display: flex;
    justify-content: center;
    overflow: hidden;
    height: 100%;
}
.TitleLink:hover {

    background-image: linear-gradient(90deg, rgba(167, 28, 28, 0.871), rgba(255, 0, 0, 0));

}
.TitleLink:hover .TitleLinkDetail{

    transform: translateX(0%);
    
}

.TitleLink.active {

    background-image: linear-gradient(90deg, rgba(233, 34, 34, 0.871), rgba(255, 0, 0, 0));
    border-left: 5px solid rgba(255, 157, 157, 0.911);
}
.TitleLinkDetailContainer{
    width: 90%;
    display: flex;
    justify-content: space-between;
    position: relative;
}
.TitleLink .TitleLinkDetail{
    position: absolute;
    background: rgb(151, 11, 11);
    width: 100px;
    text-align: center;
    box-shadow: 0px 0px 5px 3px rgba(148, 0, 0, 0.5);
    right: 0;
    transform: translateX(110%);
    transition: ease-in-out 0.2s;
}
.animButton{
    opacity: 0;
    transform: translateY(50px);
}
.animButton.active{
    transform: translateY(0);
    opacity: 1;
    transition: 0.2s ease-in;
}