@import url('https://fonts.googleapis.com/css2?family=Courier+Prime&family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;1,100;1,200;1,300;1,700&family=Oswald:wght@200;300;400;500;600;700&display=swap');

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main {
        font-family: 'Courier Prime', monospace;
font-family: 'Josefin Sans', sans-serif;
font-family: 'Oswald', sans-serif;
      -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("./images/bg.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  /* background-color: #000; */
  width: 100%;

  position: relative;
  display: flex;
  flex-direction: column;
    justify-content: center;
  
  height: 100%;

}

.newsBottom{
    padding: 10px 80px;
    border: 1px solid red;
    text-decoration: none;
    color: red;
}
.newsBottom:hover{
    background-color: rgba(255, 0, 0, 0.205);
    transition: 0.2s ease-in;
}
.Header{
    font-size: 50px;
    font-weight: 300;
}
.SubHeader{
    font-size: 20px;
    font-weight: 100;
}
.toTop{
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 10;
    border: 2px solid rgb(221, 144, 144);
    border-radius: 50%;
    background-color: rgba(182, 3, 3, 0.24);
    width: 50px;
    height: 50px;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    align-items: center;
    color: rgb(221, 144, 144);
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
.toTop:hover{
    background-color: rgba(255, 0, 0, 0.507);
}
.TopIcon{
    transform: scaleX(3);
}
