@mixin bgc($color: #fff) {
  background-color: $color;

}

@mixin size($kind:"both", $width: 100%, $height: 100%) {

  @if $kind == "both" {
    width: $width;
    height: $height;
  }
  @if $kind == "width" {
    width: $width;
  }
  @if $kind == "height" {
    height: $height;
    
  }

}