.HistoryMainContainer{
    width: 100%;

    display: flex;
    justify-content: center;
    color: #fff;
    position: relative;
    overflow: hidden;
    border-top: rgba(255, 255, 255, 0.336) solid 1px;
    background-image: 
    linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.603) 0%,
        rgba(0, 0, 0, 0.603) 100%
    ),
    url("../images/history/bg.jpg");
    background-position: center;
 
}
.historyBG{
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-50%);
    filter: brightness(0.4);
    z-index: -1;
}
.historyContainer{
    width: 80%;
    display: flex;
    flex-direction: column;
    padding: 100px 0px;
    gap: 40px;
}
.historyHeader{
    font-size: 50px;
    font-weight: 300;
    text-align: left;
}
.historyDetail{
    font-size: 20px;
    font-weight: 100;
    width: calc(40% - 20px);
    text-align: justify;
}
