.eventMainContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    color: #fff;
    position: relative;
    border-top: rgba(255, 255, 255, 0.336) solid 1px;

    background-color: rgb(0, 0, 0);
    
}
.EventContainer{
    width: 80%;
    z-index: 2;
    padding: 100px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;
    height: 100%;


}
.eventBackground{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    filter: brightness(0.3);
}
.eventDetail{
    width: 100%;


    display: flex;
    flex-direction: column;
    gap: 20px;
    
}
.EventTopButton{

    width: 100%;
    gap:5px;
    display: flex;
    flex-direction: row;


}
.EventLinkButton{
    width: calc(25%);
    border-width: 1px;
    border-style: solid;
    border-image: linear-gradient(0deg, #490404, rgb(255, 155, 155), #490404);
    border-image-slice: 1;
    background-color: rgba(0, 0, 0, 0.59);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-decoration: none;
    font-size: 20px;
    font-weight: 1000;
    overflow: hidden;
    transition: ease-in-out 0.2s;
    cursor: pointer;
}
.EventLinkButton:hover{
    color: #000;
    font-size: 35px;
    text-shadow: #ff0a0a 0px 0px 5px;
}
.EventLinkButton:hover img{
    transform: scale(1.2);
    
}
.EventLinkButton img{
    height: 60px;
    transition: ease-in-out 0.2s;
}
.EventDetailList{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    /* flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; */



}

.EventCard{
    width: 100%;
    flex-shrink: 1;
    border-top: 2px solid #000;
    background-color: rgba(34, 18, 18, 0.575);
    position: relative;
    transition: ease-in-out 0.2s;
    cursor: pointer;
    padding-bottom: 50px;

}
.EventCard.hide{
    display: none;
}


.EventCard:hover{
    background-color: rgba(133, 31, 31, 0.575);
}
.EventCard:hover .EventImg{
    transform: scale(1.2);
}
.EventImg{
    width: 100%;
    transition: ease-in-out 0.2s;
    
}
.EventCardCategory{
    position: absolute;
    top: 5px;
    left: 0;
    z-index: 2;
    width: 20%;
    background-color: rgba(0, 0, 0, 0.692);
}
.EventTitle{
    width: 95%;

}
.EventDetail{
     width: 95%;
    font-weight: 100;
    font-size: 18px;
    transform: scaleY(0.6);

}