.FooterMainContainer{
    background-color: #000;

    border-top: rgba(255, 255, 255, 0.336) solid 1px;
    
}
.FooterTop{

    padding: 30px 0px;
    display: flex;
    justify-content: center;
}
.FooterTopContainer{

    width: 80%;
}

.FooterMiddle{
    border-top: rgba(255, 255, 255, 0.336) solid 1px;
    display: flex;
    justify-content: center;
}
.FooterMiddleContainer{
    width: 80%;
    
    color: #fff;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 30px 0px;
}
.FooterMiddleLeft{
    
    padding: 0px 20px;
    display: flex;
    flex-direction: column;

}
.FooterMiddleRight{
    height: 100%;
   
}
.FooterLeftHeader, .FooterRightHeader{
    border-left: 1px solid #fff;
    padding: 5px 30px;
    font-size: 20px;
}
.SocialMediaContainer{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-left: 1px solid rgba(255, 255, 255, 0.438);
    padding: 30px 30px;
    
}
.SocialMediaContainer div{
    display: flex;
    justify-content: flex-start;
    align-items: center;

    font-weight: 100;
    cursor: pointer;
    transition: ease-in-out 0.2s;
}
.SocialMediaContainer div:hover{
    color: rgb(255, 67, 67);
}
.SocialMediaContainer img{
    height: 30px;
}
.DownloadContainer{
    display: flex;
    padding: 30px 30px;
    gap: 20px;
    border-left: 1px solid rgba(255, 255, 255, 0.438);
}
.DownloadContainer img{
    height: 50px;
    filter: brightness(0.8);
    transition: ease-in-out 0.2s;
}
.DownloadContainer img:hover{
    filter: brightness(1.5);
}
.DownloadDescription{
    padding: 0px 30px;
    padding-bottom: 20px;
    font-weight: 100;
    border-left: 1px solid rgba(255, 255, 255, 0.438);
}
.FooterBottom{
    border-top: rgba(145, 145, 145, 0.336) solid 1px;
    display: flex;
    justify-content: center;
}
.FooterBottomContainer{
    color: rgb(175, 175, 175);
    width: 80%;
    font-weight: 100;
    text-align: center;

}
.FooterBottomContainer p{
    transform: scaleY(0.7);
    padding: 30px 0px;
    font-size: 18px;
}
