.playDataMainContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.659);
     border-top: rgba(255, 255, 255, 0.336) solid 1px;
}
.PlayDataContainer{
    width: 80%;
    padding: 100px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;
    height: 100%;


}
.PlayDataDetailList{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.PlayDataCard{
    border: 1px solid rgba(109, 18, 18, 0.74);
    position: relative;
    overflow: hidden;
    cursor: pointer;
    
}
.PlayCardImage{
    width: 100%;
    filter: grayscale(100%);
}
.PlayCardLogo{
    width: 30%;
    position: absolute;
    top: 0;
    left: 0;
    transition: ease-in-out 0.3s;
    background-color: rgba(0, 0, 0, 0.671);
}
.PlayDataCard:hover .PlayCardLogo{
    top: 0;
    background-color: rgba(0, 0, 0, 0.347);
}
.PlayDataCard:hover .PlayCardImage{
    filter: grayscale(0%);
    transition: ease-in-out 0.3s;
}
.PlayDataCard:hover .PlayDataViewMore{
    height: 50px;
    color: #fff;
}
.PlayDataViewMore{
    position: absolute;
    z-index: 2;
    bottom: 0;
    background: rgba(46, 3, 3, 0.781);
    width: 100%;
    text-align: center;
    border-top: 1px solid rgba(255, 0, 0, 0.308);
    transition: ease-in-out 0.3s;
    height: 10px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: transparent;

    
}